<template>
  <div class="image">
		<div v-if='data.length != 0'>
			<swiper  :options="swiperOption" ref="mySwiper">
				<swiper-slide :key='index'  v-for='(item,index) in data.images'>
					<img class='image-img'  :src='item.imgUrl'>
				</swiper-slide>
				<div class="swiper-pagination"  slot="pagination"></div>
			</swiper>
		</div>
		<div class='goodsDetail-content'>
			<div class='goodsDetail-item'>
				<div class='goodsDetail-price'>{{data.price}}</div>
				<div class='goodsDetail-name'>{{data.name}}</div>
			</div>
			<div class='goodsDetail-share'>
				<img src='/public/image/goodsDetails/header/.png' class='share'>
				<div></div>
			</div>
			<div class='goodsDetail-courier'>:{{data.courier}}</div>
			<div class='goodsDetail-sales'>{{data.sales}}</div>
			<div class='goodsDetail-address'>{{data.address}}</div>
		</div>
  </div>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import {swiper, swiperSlide} from 'vue-awesome-swiper'
export default {
  name: 'goodsDetail-content',
  data () {
    return {
			swiperOption:{
				autoplay:{
					autoplay:true,
					delay: 3000,
					disableOnInteraction: false,
				},
				loop: true,
				speed: 1000,
				observer:true,//swiperswiper
				observeParents:true,//swiperswiper
			},
			data:{
				
			}
    }
  },
	components:{
		swiper,	
		swiperSlide
	},
	methods:{
		getGoodsDate:function(){
			this.axios({
				method:'post',
				url:'getGoodsDate',
				data:{
					id:1
				}
			}).then((res)=>{
				this.data = res.data.data;
			})
		}
	},
	created(){
		this.getGoodsDate();
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
*{
	margin:0;
	padding:0;
}
.image{
	border-bottom:1px solid #eee;
	margin-top:12vw;
}
.image-img{
	width:100vw;
}
html{
	background:red;
}
.goodsDetail-content{
	display:flex;
	flex-flow:row wrap;
	justify-content:space-between;
	align-items:center;
	background:#fff;
	padding:2vw 2vw;
}
.goodsDetail-item{
	font-size:6vw;
}
.goodsDetail-name{
	width:85vw;
	font-size:4.2vw;
}
.goodsDetail-price{
	color:red;
}
.goodsDetail-share{
	display:flex;
	justify-content:center;
	align-items:center;
	flex-flow:column wrap;
	font-size:3vw;
}
.goodsDetail-courier,.goodsDetail-sales,.goodsDetail-address{
	color:#999;
	font-weight:500;
	font-size:4vw;
	margin-top:1vw;
}
.share{
	width:6vw;
	margin-bottom:0.5vw;
}
</style>



