<template>
  <div class="my">
		<div class='my-information'>
			<img :src='information.headimg' class='my-information-img'>
			<div class='my-information-text'>{{information.name}}</div>
			<div class='my-information-go'>></div>
		</div>
		<div class='my-menus'>
			<div class='my-menu-item' v-for='(item,index) in menus' @click.stop='go(item.url)'>
				<img :src='item.imgUrl' class='my-menu-img'>
				<div class='my-menu-text'>{{item.name}}</div>
				<img src='/public/image/my/menus/.png' class='my-menu-go'>
			</div>
		</div>
		
		<div class='my-otherMenus'>
			<div class='my-menu-item' v-for='(item,index) in otherMenus'>
				<img :src='item.imgUrl' class='my-menu-img'>
				<div class='my-menu-text'>{{item.name}}</div>
				<img src='/public/image/my/menus/.png' class='my-menu-go'>
			</div>
		</div>
  </div>
</template>

<script>

export default {
  name: 'HelloWorld',
  data () {
    return {
			information:{
				headimg:'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1537521809730&di=fef5e4bf4b9aa50d8e9979476b912151&imgtype=0&src=http%3A%2F%2Fimgsrc.baidu.com%2Fforum%2Fw%3D580%2Fsign%3D8724f7443dd12f2ece05ae687fc3d5ff%2F44cd8bd4b31c87015d4324752c7f9e2f0508ffef.jpg',
				name:''
			},
      menus:[
				{name:'',imgUrl:'/public/image/my/menus/.png',url:'address'},
				{name:'',imgUrl:'/public/image/my/menus/.png',url:'order'},
				{name:'',imgUrl:'/public/image/my/menus/.png',url:'shoppingCart'},
				{name:'',imgUrl:'/public/image/my/menus/.png',url:''},
				{name:'',imgUrl:'/public/image/my/menus/.png',url:''},
				{name:'',imgUrl:'/public/image/my/menus/.png',url:''},
				{name:'',imgUrl:'/public/image/my/menus/.png',url:''},
			],
			otherMenus:[
				{name:'',imgUrl:'/public/image/my/menus/.png',url:''},
			]
    }
  },
	components:{
		
	},
	methods:{
		go(url){
			console.log(url)
			this.$router.push({name:url});
		}
	},
	created(){
		
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
*{
	margin:0;
	padding:0;
}
.my{
	margin-bottom:15vw;
}
.my-information{
	height:30vw;
	display:flex;
	justify-content:flex-start;
	align-items:center;
	position:relative;
	background:url('http://img04.tooopen.com/images/20130701/tooopen_20083555.jpg');
	background-size:100% 200%;
}
.my-information-img{
	width:15vw;
	border-radius:50%;
	margin-left: 8vw;
}
.my-information-text{
	font-size:5vw;
	margin-left: 4vw;
	color:#fff;
}
.my-information-go{
	position:absolute;
	right:4vw;
	width:6vw;
	color:#fff;
	font-size:8vw;
}
.my-menus{
	
}
.my-menu-item{
	display:flex;
	justify-content:flex-start;
	align-items:center;
	position:relative;
	height:12vw;
	border-bottom:1px solid #eee;
	background:#fff;
}
.my-menu-img{
	width:6vw;
	height:6vw;
	margin:1vw;
}
.my-menu-text{
	font-size:3vw;
}
.my-menu-go{
	position:absolute;
	right:2vw;
	width:5vw;
}
.my-otherMenus{
	margin-top:4vw;
	border-top:1px solid #eee;
	border-bottom:1px solid #eee;
}
</style>



