<template>
  <div class="orderItem">
		<div class='orderItem-item' v-for='(item,index) in data' :key='item.id'>
			<div class='orderItem-status'>{{item.status | Title}}</div>
			<div class='orderItem-content' v-for='(data,index) in item.item' :key='index'>
				<img class='orderItem-img' :src='data.src'>
				<div class='orderItem-text'>
					<div class='orderItem-title'>{{data.title}}</div>
					<div class='orderItem-classification'>{{data.fenlei}}</div>
				</div>
				<div class='orderItem-unit'>
					<div class='orderItem-unit-price'>{{data.danjia}}</div>
					<div class='orderItem-nums'>x{{data.shuliang}}</div>
				</div>
			</div>
			<div class='orderItem-total-price'>{{item.shuliang}} :{{item.price}}</div>
			<div class='orderItem-bar'>
				<div v-if='item.status == 1' class='orderItem-bar-item'></div>
				<div v-if='item.status == 2' class='orderItem-bar-item'></div>
				<div v-if='item.status == 3' class='orderItem-bar-item'></div>
        <div v-if='item.status == 4' class='orderItem-bar-item'></div>
			</div>
		</div>
  </div>
</template>

<script>
export default {
  name: 'orderItem',
	props:['id'],
  data () {
    return {
			data:[]
    }
  },
	components:{
		
	},
	methods:{
		getList(id){
			this.axios({
				url:'getOrderList',
				method:'get',
				data:{
					id:id
				}
			}).then(res=>{
				console.log(res)
				if(res.data.status == 200){
					this.data = res.data.data;
				}
			})
		}
	},
	created(){
		this.getList(1);
	},
	watch:{
		id(val,oldVal){
      this.getList(val);
		}
	},
  filters:{
    Title(val){
      return val == 1 ? "" : val == 2 ? "" : val == 3 ? "" : val == 4 ? "" : "";
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.orderItem{
	padding:2vw;
	display:flex;
	flex-flow:column wrap;
	justify-content:flex-start;
	align-items:center;
	padding:4vw;
	background:#eee;
	font-size:3.2vw;
}
.orderItem-item{
	background:#fff;
	width:90vw;
	margin:2vw;
	border-radius:4vw;
	padding:2vw 2vw;
}
.orderItem-status{
	text-align:right;
}
.orderItem-content{
	display:flex;
	justify-content:flex-start;
	align-items:flex-start;
  flex:1;
  margin:2vw 0vw;
}
.orderItem-img{
	width:20vw;
	height:20vw;
}
.orderItem-text{
  flex:1;
  margin:0vw 2vw;
}
.orderItem-title{
  font-size:3.5vw;
}
.orderItem-classification{
  color:#aaa;
  font-size:3.2vw;
}
.orderItem-unit{
  text-align:right;
}
.orderItem-unit-price{
  
}
.orderItem-nums{
  color:#aaa;
}
.orderItem-total-price{
	text-align:right;
}
.orderItem-bar{
	display:flex;
	justify-content:flex-end;
	align-items:center;
  margin-top:3vw;
}
.orderItem-bar-item{
  font-size:3.2vw;
  color:#aaa;
  border:1px solid #aaa;
  border-radius:5vw;
  margin-left:2vw;
  padding:1vw 3vw;
}
</style>



