<template>
  <div class="classificationDetailsItem">
		<!-- -start -->
		<div class='classifyGoods-item'>
			<div class='classifyGoods-content' v-for='(item,index) in data' :key='index' @click.stop='go(item.id)'>
				<img :src='item.imgUrl' class='classifyGoods-img'>
				<div class='classifyGoods-text'>{{item.text}}</div>
				<div class='classifyGoods-price-sold'>
					<div class='classifyGoods-price'>{{item.price}}</div>
					<div class='classifyGoods-sold'>{{item.sold}}</div>
				</div>
			</div>
		</div>
		<!-- -end -->
		
		<!-- -start -->
		<div class='loading'>
			<div></div>
		</div>
  </div>
</template>

<script>
export default {
  name: 'classificationDetailsItem',
	components:{
	},
  data () {
    return {
      data:[]
    }
  },
	methods:{
		getMenusData:function(status){//
			this.axios({
				url:'classifGoods',
				method:'get',
				params:{
					id:status
				}
			}).then((res)=>{
        console.log(res.data.data)
				if(res.data.status == 200){
          this.data = res.data.data;
				}else{
					alert("")
				}
			})
		},
    go(data){
      console.log(data);
    }
	},
	created(){
    this.getMenusData();
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.classificationDetailsItem{
	margin-top:12vw;
}
.classifyGoods-item{
	width:100%;
	display:flex;
	justify-content:flex-start;
	align-items:center;
	flex-flow: row wrap;
	overflow:hidden;
	margin-top:2vw;
}
.classifyGoods-content{
	width:48vw;
	margin:1vw;
	overflow:hidden;
}
.classifyGoods-img{
	width:48vw;
	height:50vw;
	border-radius: 1vw 1vw;
}
.classifyGoods-text{
	font-size:3vw;
}
.classifyGoods-price-sold{
	display:flex;
	justify-content:space-between;
	align-items:center;
	font-size:2vw;
}
.classifyGoods-price{
	color:red;
}
.classifyGoods-sold{
	color:#999;
}
.loading{
	display:flex;
	justify-content:center;
	align-items:center;
	color:#eee;
	font-size:2vw;
}
</style>



