<template>
	<div class='coupons'>
		<div class='coupons-item'>
			<img @click='dosome(index)' :src='item.imgUrl' v-for='(item,index) in data' :key='index' class='coupons-img'>
		</div>
	</div>
</template>

<script>
export default {
  name: 'coupons',
  data () {
    return {
			data:[]
    }
  },
	methods:{
		getList:function(){
			this.axios({
				url:'coupons',
				method:'get',
			}).then((res)=>{
				if(res.data.status == 200){
					this.data = res.data.data
				}else{
					alert("")
				}
			})
		},
		dosome:function(data){
			// this.$router.push({name:data})
		}
	},
	created:function(){
		this.getList();
	}
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.coupons{
	height:15vw;
	overflow:hidden;
}
.coupons-item{
	width:100%;
	white-space: nowrap;
	overflow: scroll;
	height:30vw;
}
.coupons-img{
	display:inline-block;
	width:25vw;
	margin:1vw;
}
</style>



