<template>
  <div class="hello">
    <!-- headder-start -->
			<banner></banner>
    <!-- headder-end -->
		
		
		<!-- -start -->
			<classify></classify>
		<!-- -end -->
		
		
		<!-- -start -->
			<message></message>
		<!-- -end -->
		
		<!-- -start -->
			<spellGroup></spellGroup>
		<!-- -end -->
		
		<!-- -start -->
			<activity></activity>
		<!-- -end -->
		
		<!-- -start -->
			<like></like>
		<!-- -end -->
		
		<!-- -start -->
			<coupons></coupons>
		<!-- -end -->
		
		<!-- -start -->
			<goods v-for='(item,index) in goodsData' :data='item' :key='index'></goods>
		<!-- -end -->
		
		<!-- -start -->
			<classifyGoods></classifyGoods>
		<!-- -end -->
  </div>
</template>

<script>
import banner from './banner'
import classify from './classify'
import message from './message'
import spellGroup from './spellGroup'
import activity from './activity'
import like from './like'
import coupons from './coupons'
import goods from './goods'
import classifyGoods from './classifyGoods'
export default {
  name: 'HelloWorld',
  data () {
    return {
      goodsData:[
				{imgUrl:'',goods:[{imgUrl:'',text:'t',price:198}]},
			]
    }
  },
	components:{
		banner,
		classify,
		message,
		spellGroup,
		activity,
		like,
		coupons,
		goods,
		classifyGoods,
	},
	methods:{
		getGoodsList:function(){
			this.axios({
				url:'goods',
				method:'get',
			}).then((res)=>{
				if(res.data.status == 200){
					this.goodsData = res.data.data;
				}else{
					alert("")
				}
			})
		}
	},
	created(){
		this.getGoodsList();
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
*{
	margin:0;
	padding:0;
}
.hello{
	margin-bottom:15vw;
}
</style>



