<template>
  <div v-if='data.length != 0' class="detail-footer">
		<div class='detail-go' @click.stop='go("index")'>
			<img class='detail-go-img' src='public/image/goodsDetails/footer/.png'>
			<div></div>
		</div>
		<div class='detail-go' @click.stop='go("service")'>
			<img class='detail-go-img' src='public/image/goodsDetails/footer/1.png'>
			<div></div>
		</div>
		<div class='detail-go'>
			<img class='detail-go-img' src='public/image/goodsDetails/footer/1.png'>
			<div></div>
		</div>
		<div class='detail-operation'>
			<div class='detail-cart' @click.stop='show("show")'></div>
			<div class='detail-buy'></div>
		</div>
		<div v-if='layerFlag' class='mask'>
			<div class='layer'>
				<div class='detail-layer-content'>
					<img :src='data.imgUrl' class='detail-layer-img'>
					<div class='detail-layer-informaition'>
						<div class='detail-layer-price'><span></span>{{data.price}}</div>
						<div class='detail-layer-sales'>{{data.sales}}</div>
						<div class='detail-layer-choose'></div>
					</div>
				</div>
				<div class='detail-layer-classification' ref='scrolly'>
					<div class='detail-layer-classification-scroll'>
						<div class='detail-layer-classification-item' v-for='(item,index) in data.list' :key='index'>
							<div class='detail-layer-classification-title'>{{item.name}}</div>
							<div class='flex-row-wrap'>
								<div @click.stop='showlist(list)' class='detail-layer-classification-list' v-for='(list,index) in item.data' :key='index'>{{list.text}}</div>
							</div>
						</div>
						<div class='flex-row-between detail-layer-operation'>
							<div></div> 
							<div class='detail-layer-operation-item flex-row-wrap'>
								<div class='operation-btn' @click.stop='subtract'>-</div>
								<div>{{num}}</div>
								<div class='operation-btn' @click.stop='add'>+</div>
							</div>
						</div>
					</div>
				</div>
				
				<div class='detail-layer-btn-item'>
					<div class='layer-buttom-btn' @click='show("close")'></div>
				</div>
			</div>
		</div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
export default {
  name: 'goodsDetail-footer',
  data () {
    return {
			data:[],
			layerFlag:false,
			num:1
    }
  },
	methods:{
		go(data){
			console.log(data);
		},
		getClassificationDetails(){
			this.axios({
				url:'getClassificationDetails',
				method:'post',
				data:{
					id:1
				}
			}).then((res)=>{
				this.data = res.data.data;
				console.log()
				
			})
		},
		show(data){
			console.log(data);
			if(data == 'show'){
				this.layerFlag = true;
			}else{
				this.layerFlag = false;
			}
			
		},
		showlist(data){
			console.log(data);
		},
		add(){
			this.num++
			console.log("aaa")
		},
		subtract(){
			if(this.num == 1){
				return false;
			}
			this.num--
			console.log(this.num)
		}
	},
	created(){
		this.getClassificationDetails();
	},
	watch:{
		layerFlag(val,oldVal){
			if(val){
				this.$nextTick(()=>{
					let wrapper = document.querySelector('.detail-layer-classification');
					console.log(wrapper.offsetHeight);
					wrapper.style.height = '52vh';
					console.log(wrapper);
					let scroll = new BScroll(wrapper,{
						click:true
					})
				})
				
			}
			
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.detail-footer{
	position:fixed;
	bottom:0vw;
	display:flex;
	z-index:5;
	align-items:center;
	border-top:1px solid #eee;
	width:100vw;
	height:12vw;
	background:#fff;
}

.detail-go{
	display:flex;
	flex-flow:column wrap;
	align-items:center;
	margin:0vw 1vw;
	flex:1;
	font-size:3vw;
}
.detail-go-img{
	width:5vw;
	height:5vw;
}
.detail-operation{
	display:flex;
	justify-content:center;
	align-items:center;
	width:60vw;
	height:9vw;
	border-radius:8vw 8vw;
	margin:0vw 1vw;
	font-size:3vw;
	color:#fff;
	overflow:hidden;
}
.detail-cart{
	width:30vw;
	height:100%;
	display:flex;
	justify-content:center;
	align-items:center;
	background:linear-gradient(to right,#ffc802,#fd8b04);
}
.detail-buy{
	width:30vw;
	height:100%;
	display:flex;
	justify-content:center;
	align-items:center;
	background:linear-gradient(to right,#ff7301,#ff4b03);
}


.detail-layer-content{
	display:flex;
	flex-flow:row wrap;
	height:21vh;
}
.detail-layer-img{
	width:20vh;
	height:20vh;
	border-radius:1vw;
}
.detail-layer-informaition{
	display:flex;
	flex-flow:column wrap;
	justify-content:center;
	align-items:flex-start;
}
.detail-layer-price{
	color:red;
	margin:2vw;
	font-size:5vw;
}
.detail-layer-sales{
	margin:2vw;
	font-size:3.2vw;
	color:#999;
}
.detail-layer-choose{
	margin:2vw;
	font-size:4vw;
}
.detail-layer-classification{
	height:52vh;
	background:#fff;
	overflow:hidden;
}
.detail-layer-classification-scroll{
	overflow:scroll;
	overflow-x:hidden;
	padding-right:4vw;
	width:98vw;
}
.detail-layer-classification-item{
	margin-bottom:2vw;
}
.detail-layer-classification-title{
	font-size:4vw;
	border-bottom:1px solid #eee;
	
}
.detail-layer-classification-list{
	font-size:3.5vw;
	padding:1vw 2vw;
	border-radius: 6vw 6vw;
	margin:1vw;
	background:#eee;
	overflow:hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}
.detail-layer-operation{
	padding:2vw;
	font-size:3.5vw;
	border-top:1px solid #eee;
}
.detail-layer-operation-item{
	margin-right:6vw;
}
.operation-btn{
	width:2vw;
	margin:0vw 1vw;
}
.detail-layer-btn-item{
	border-top:1px solid #eee;
	height:7vh;
	display:flex;
	justify-content:center;
	align-items:center;
}



</style>



