<template>
  <div v-if='data.length != 0' class="evaluation">
		<div class='evaluation-nums'>({{data.evaluationNums[0].nums}})</div>
		<div class='evaluation-more'>
			<div></div>
			<img class='evaluation-more-img' src='/public/image/goodsDetails/evaluation/.png'>
		</div>
		<div class='evaluation-menu'>
			<div @click='go(item.id)' class='evaluation-item' v-if='index != 0' v-for='(item,index) in data.evaluationNums'>{{item.name}} ({{item.nums}})</div>
		</div>
		<div class='evaluationDate'>
			<div class='evaluationDate-people'>
				<img class='evaluationDate-headerimg' :src='data.evaluationDate.headerimg'>
				<div class='evaluationDate-name'>{{data.evaluationDate.name}}</div>
			</div>
			<div class='evaluationDate-text'>{{data.evaluationDate.text}}</div>
		</div>
  </div>
</template>

<script>
export default {
  name: 'evaluation',
  data () {
    return {
			data:[],
			flag:1
    }
  },
	methods:{
		getEvaluationDate:function(){
			this.axios({
				method:'post',
				url:'getEvaluationDate',
				data:{
					id:1
				}
			}).then((res)=>{
				this.data = res.data.data;
			})
		},
		go:function(data){
			console.log("",data)
		}
	},
	created(){
		this.getEvaluationDate();
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.evaluation{
	margin-top:1vw;
	background:#fff;
	padding-left:2vw;
	position:relative;
	padding-bottom:2vw;
}
.evaluation-nums{
	font-size:4vw;
	padding-top:2vw;
}
.evaluation-more{
	position:absolute;
	top:2vw;
	right:2vw;
	display:flex;
	align-items:center;
	font-size:3.2vw;
	color:red;
}
.evaluation-more-img{
	width:4vw;
	height:4vw;
	margin: 0vw 1vw;
}
.evaluation-menu{
	display:flex;
	margin:2vw 0vw;
}
.evaluation-item{
	font-size:3vw;
	margin-right:2vw;
	background:#ffebea;
	border:1px solid #ffebea;
	color:#645656;
	display:flex;
	justify-content:center;
	align-items:center;
	padding:0.5vw 2vw;
	border-radius:5vw 5vw;
}
.evaluationDate{
	display:flex;
	position:relative;
	flex-flow:column wrap;
}
.evaluationDate-people{
	display:flex;
	align-items:center;
}
.evaluationDate-headerimg{
	width:6vw;
	height:6vw;
	border-radius:50%;
}
.evaluationDate-name{
	margin-left:2vw;
	font-size:3.5vw;
	color:#999;
}
.evaluationDate-text{
	color:#464646;
	font-size:4vw;
	margin-top:1.5vw;
}

</style>



