<template>
  <div v-if='cartdata.length != 0' class="cart">
		<dele v-if='!show' :cartdata='cartdata' @change='change'></dele>
		<div class='cart-item'>
			<div v-for='(item,index) in cartdata' class='rongqi'>
				<div class='flex-row-wrap align-top'>
					<input class='checkbox' type='checkbox' v-model='item.flag' @change='change(index)'>
					<div class='flex-row-wrap align-top'>
						<img :src='item.imgUrl' class='cart-store-logo'>
						<div class=''>
							<div class='cartInformaition'>
								<div class='cartInformaition-text'>{{item.text}}</div>
								<div class='cartInformaition-type'>{{item.type}}</div>
							</div>
							<div class='flex-row-between'>
								<div class='cartitem-price'>{{item.price}}</div>
								<div class='flex-row-wrap'>
									<div @click='subtract(index)' class='cart-btn'>-</div>
									<div class='cart-nums'>{{item.nums}}</div>
									<div @click='add(index)' class='cart-btn'>+</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
  </div>
</template>

<script>
import dele from './dele'
export default {
  name: 'cart',
	props:['cartdata','show'],
  data () {
    return {
			
    }
  },
	components:{
		dele,
	},
	methods:{
		change(index){
			this.$emit('change',index)
		},
		add(index){
			this.cartdata[index].nums++
		},
		subtract(index){
			if(this.cartdata[index].nums == 1){
				return false;
			}
			this.cartdata[index].nums--
		}
	},
	created(){
		
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.cart{
	margin-bottom:28vw;
	min-height:100vh;
	background:#eee;
	margin-top:15vw;
}
.cart-item{
	padding:0vw 4vw 4vw 4vw;
	padding-top:2vw;
}
.rongqi{
	margin-bottom:4vw;
	border:1px solid #fff;
	border-radius:3vw;
	padding:3vw 2vw 3vw 2vw;
	background:#fff;
}
.checkbox{
	margin-top:8vw;
}
.cart-store-logo{
	width:20vw;
	height:20vw;
	border-radius:1vw;
	margin:0vw 2vw;
}
.cartInformaition{
	font-size:3.2vw;
	width:55vw;
}
.cartInformaition-text{
	margin-bottom:2vw;
	font-size:3.5vw;
	letter-spacing:1px;
}
.cartInformaition-type{
	background:#f9f9f9;
	border-radius:1vw;
	font-size:3.3vw;
	color:#888;
}
.flex-row-between{
	margin-top:5vw;
}
.cartitem-price{
	color:red;
}
.cart-btn{
	width:5vw;
	height:5vw;
	font-size:3.2vw;
	border:1px solid #eee;
	display:flex;
	justify-content:center;
	align-items:center;
}
.cart-nums{
	width:10vw;
	height:5vw;
	font-size:3.2vw;s
	text-align:center;
	border-top:1px solid #eee;
	border-bottom:1px solid #eee;
	display:flex;
	justify-content:center;
	align-items:center;
}
</style>



