<template>
	<div class='goods'>
		<img :src='data.imgUrl' class='goods-banner'>
		<div class='goods-item-height'>
			<div class='goods-scroll'>
				<div @click='go(item.url)' class='goods-item' v-for='(item,index) in data.goods'>
					<div class='goods-item-flex'>
						<img :src='item.imgUrl' class='goods-img'>
						<div class='goods-text'>{{item.text}}</div>
						<div class='goods-price'>{{item.price}}</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
  name: 'goods',
  data () {
    return {
			
    }
  },
	methods:{
		go:function(data){
			// this.$router.push({name:data})
		}
	},
	created:function(){
		
	},
	props:['data']
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.goods{
	overflow:hidden;
}
.goods-banner{
	width:100%;
	height:33vw;
}
.goods-item-height{/**/
	height:49vw;
	overflow:hidden;
}
.goods-scroll{/**/
	width:100%;
	white-space: nowrap;
	overflow-x: scroll;
	height:51vw;
}
.goods-item{
	display:inline-block;
	width:30vw;
	margin:0.5vw;
}
.goods-item-flex{
	display:flex;
	flex-flow: column wrap;
	align-items:center;
}
.goods-img{
	width:30vw;
	height:36vw;
}
.goods-text{
	font-size:3vw;
	margin:1vw;
}
.goods-price{
	font-size:2vw;
	color:red;
}
</style>



