<template>
  <div class='list'>
		<div class='meuns'>
			<div class='menu-item' @click.stop='changeMenus(index,item.id)' v-for='(item,index) in menus' :class='index == menuFlag ? "select":""'>{{item.name}}</div>
		</div>
		<div class='list-secondary-classification'>
			<div class='list-secondary-classification-item' v-for='(item,index) in listItem' @click.stop='go(index,item.name)'>
				<img class='list-secondary-classification-img' :src='item.imgUrl'>
				<div class='list-secondary-classification-text'>{{item.name}}</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
  name: 'list',
  data () {
    return {
			menus:[],
			menuFlag:0,
			listItem:[]
    }
  },
	methods:{
		go:function(index,data){
			console.log('',data);
			this.$router.push({name: 'classificationDetails',query:{name:data}});
		},
		getMenus:function(){
			this.axios({
				method:'get',
				url:'getmenus'
			}).then((res)=>{
				console.log(res);
				this.menus = res.data.data;
			})
		},
		changeMenus:function(index,data){
			console.log(index,data)
			this.menuFlag = index;
			this.axios({
				method:'post',
				url:'getMenuData',
				data:{
					id:data
				}
			}).then((res)=>{
				console.log(res);
				if(res.data.status == 200){
					this.listItem = res.data.data;
				}
			})
		}
	},
	created:function(){
		this.getMenus();
		this.changeMenus(0,0)
	}
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.list{
	display: flex;
	justify-content: flex-start;
	align-items: flex-start;
	flex-flow: row wrap;
	position: relative;
}
.meuns{
	width:20vw;
}
.menu-item{
	height:10vw;
	border:1px solid #eee;
	font-size:3vw;
	display:flex;
	justify-content:center;
	align-items:center;
}
.select{
	color:#ec6d69;
	background:#f5f5f5;
}

.list-secondary-classification{
	width:80vw;
	overflow:hidden;
	display:flex;
	justify-content:flex-start;
	align-items:center;
	flex-flow:row wrap;
	box-sizing:border-box;
	border-left:1px solid #eee;
}
.list-secondary-classification-item{
	width:37.5vw;
	overflow:hidden;
	margin:1vw;
	display:flex;
	justify-content:flex-start;
	align-items:center;
	flex-flow:column wrap;
}
.list-secondary-classification-img{
	width:38vw;
}
.list-secondary-classification-text{
	font-size:3vw;
	margin-top:2vw;
	margin-bottom:3vw;
}
</style>



