<template>
  <div class="orderHeader">
    <div class='overflowx'>
      <div class='menus' v-for='(item,index) in data' :key='item.id' :class='indexFlag == index ? "select":""' @click.stop='change(index)'>{{item.name}}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'orderHeader',
  data () {
    return {
      data:[
				{name:'',id:8},
				{name:'',id:0},
				{name:'',id:1},
        {name:'',id:3},
        {name:'',id:6},
        {name:'',id:7},
				{name:'',id:2},
				{name:'',id:4},
				{name:'',id:5},
				
			],
			indexFlag:0
    }
  },
	components:{
		
	},
	methods:{
		change(index){
			this.indexFlag = index;
			this.$emit('id',this.data[index].id)
		}
	},
	created(){
		
	},
	watch:{
		
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.orderHeader{
	padding:2vw;
	margin-top:12vw;
  overflow:hidden;
  height:10vw;
  width:96vw;
}
.overflowx{
  overflow-x:scroll;
  white-space: nowrap;
  width:100vw;
  height:15vw;
  margin-right:4vw;
}
.menus{
	display:inline-block;
  padding:2vw;
  font-size:3.5vw;
}
.select{
	font-size:4vw;
	border-bottom:1px solid red;
	color:red;
}
</style>



