<template>
  <div class='header'>
		<div class='bar'>
			<img src='/public/image/index/header/list.png' @click='go("classification")' class='list'>
			<input type='text' @click='go("search")' class='search'>
		</div>
		<div v-if='swiperData.length != 0'>
			<swiper  :options="swiperOption" ref="mySwiper">
				<swiper-slide :key='index'  v-for='(item,index) in swiperData'>
					<img  :src='item.src'>
				</swiper-slide>
				<div class="swiper-pagination"  slot="pagination"></div>
			</swiper>
		</div>
	</div>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import {swiper, swiperSlide} from 'vue-awesome-swiper'
var vm = null;
export default {
  name: 'banner',
	components:{
		swiper,
		swiperSlide
	},
  data () {
    return {
			swiperOption:{
				autoplay:{
					autoplay:true,
					delay: 3000,
					disableOnInteraction: false,
				},
				loop: true,
				speed: 1000,
				observer:true,//swiperswiper
				observeParents:true,//swiperswiper
				on:{
					click:function(){
						vm.go(vm.swiperData[this.realIndex].url)
					}
				}
			},
			swiperData:[]
    }
  },
	methods:{
		go:function(data){
			this.$router.push({name: data});
		},
		getbanner:function(){
			this.axios({
				url:'banner',
				method:'get',
			}).then((res)=>{
				if(res.data.status == 200){
					this.swiperData = res.data.data;
				}else{
					alert("")
				}
			})
		}
	},
	computed:{
		swiper(){
			return this.$refs.mySwiper.swiper;
		}
	},
	created:function(){
		this.getbanner();
		vm = this;
	}
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.header{
	background:#f4f5f9;
}
img{
	width:100%;
	height:50vw;
}
.bar{
	position:absolute;
	top:2vw;
	z-index:2;
	display:flex;
	justify-content:flex-start;
	align-items:flex-start;
	width:100%;
}
.list{
	width:8vw;
	height:6vw;
	margin-right:3vw;
	margin-left:3vw;
}
.search{
	width:70vw;
	height:6vw;
	border-radius:5vw 5vw;
	border:1px solid #fff;
	background:#fff5;
	padding-left:5vw;
}
.search:focus{
	outline: none;
}
</style>



