import headhistory from'../history/headhistory'
import orderHeader from'./orderHeader'
import orderItem from'./orderItem'
export default {
  name: 'order',
  data () {
    return {
      title:'',
			id:1
    }
  },
	components:{
		orderHeader,
		orderItem,
		headhistory,
	},
	methods:{
		show(id){
			this.id = id;
			/*this.axios({
				url:'',
				params:{
					id:id
				},
				method:'get'
			})*/
		}
	},
	created(){
		
	},
	watch:{
		
	}
}
</script>

