<template>
  <div v-if='show' class="shopping-cart-footer">
		<div class='footer-left'>
			<input type='checkbox' v-model='flag' @change='change(flag)'> 
			<div></div>
		</div>
		<div class='footer-right'>
			<div>:{{price}}</div>
			<div class='footer-right-btn' @click='jiesuan'></div>
		</div>
  </div>
</template>

<script>
export default {
  name: 'shoppingCartFooter',
  data () {
    return {
			flag:false,
			price:0
    }
  },
	props:['cartdata','show'],
	methods:{
		change(data){
			console.log('',data);
			for(let i of this.cartdata){
				i.flag = data;
			}
		},
		jiesuan(){
			console.log(this.price);
			alert(':'+this.price)
		}
	},
	created(){
		
	},
	watch:{
		cartdata:{//cartdata
			handler(val,oldVal){
				this.flag = true;
				this.price = 0;
				for(let i of this.cartdata){
					if(i.flag == false){
						this.flag = false;
					}else{
						this.price += i.price*i.nums;//,,
					}
				}
			},
			deep:true
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.shopping-cart-footer{
	position:fixed;
	bottom:12vw;
	width:100vw;
	display:flex;
	justify-content:space-between;
	align-items:center;
	height:15vw;
	border-top:1px solid #eee;
	background:#fff;
}
.footer-left,.footer-right{
	display:flex;
	align-items:center;
	font-size:4vw;
}
.footer-right-btn{
	padding:2vw 5vw;
	background:#ffcc01;
	font-size:3vw;
	margin: 0vw 2vw;
	border-radius:1vw;
}
input{
	margin:0vw 2vw;
	margin-left:4vw;
}

</style>



