<template>
  <div class="address">
		<headhistory :data='title' v-if='id == null'></headhistory>
		<container v-if='id == null' @edit='edit'></container>
		<editAddress v-if='id != null' :id='id' @hide='hide'></editAddress>
  </div>
</template>

<script>
import headhistory from'../history/headhistory'
import container from'./content'
import editAddress from'./addaddress'
export default {
  name: 'HelloWorld',
  data () {
    return {
      sheng:[],
			id:null,
			title:''
    }
  },
	components:{
		container,
		editAddress,
		headhistory
	},
	methods:{
		edit(data){
			this.id = data;
		},
		hide(){
			this.id = null;
		}
	},
	created(){
		
	},
	watch:{
		
	}
}
</script>

