<template>
  <div class="goodsDetails">
		<headhistory :data='title' v-if='id == null'></headhistory>
		<!-- -start -->
			<imgs></imgs>
		<!-- -end -->
		
		<!-- -start -->
			<coupons></coupons>
		<!-- -end -->
		
		<!-- -start -->
			<evaluation></evaluation>
		<!-- -end -->
		
		<!-- -start -->
			<store></store>
		<!-- -end -->
		
		<!-- -start -->
			<detailsFooter></detailsFooter>
		<!-- -end -->
  </div>
</template>

<script>
import headhistory from '../history/headhistory' 
import imgs from './image' 
import evaluation from './evaluation' 
import store from './store' 
import coupons from './coupons' 
import detailsFooter from './detailsFooter' 
export default {
  name: 'goodsDetails',
  data () {
    return {
			title:''
    }
  },
	components:{
		headhistory,
		imgs,
		evaluation,
		store,
		coupons,
		detailsFooter,
	},
	methods:{
		
	},
	created(){
		if(this.$route.query.name == undefined){
			this.$router.push({name:'index'})
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.goodsDetails{
	margin-bottom:12vw;
	background:#f5f5f5;
}
</style>



