<template>
	<div class='list'>
		<div class="head-item">
			<div v-for="(item,index) in list" class='flex' @click='go(item.url)'>
				<div class='list-img'>
					 <img :src='item.src'>
				</div>
				<div class='list-text'>{{item.text}}</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
  name: 'classify',
  data () {
    return {
			list:[
				
			]
    }
  },
	methods:{
		getList:function(){
			this.axios({
				url:'list',
				method:'get',
			}).then((res)=>{
				if(res.data.status == 200){
					this.list = res.data.data
				}else{
					alert("")
				}
			})
		},
		go:function(data){
			// this.$router.push({name:data})
		}
	},
	created:function(){
		this.getList();
	}
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.list{
	background:#f4f5f9;
	padding-bottom:2vw;
}
.head-item{
	display:flex;
	justify-content:center;
	align-items:center;
	flex-flow:row wrap;
}
.flex{
	display:flex;
	justify-content:center;
	align-items:center;
	flex-flow:column wrap;
	width:23vw;
	margin-top:2vw;
}
.list-img{
	width:11vw;
	height:11vw;
	border-radius:100%;
	background:linear-gradient(#23dfc6, #2fd1cc);
	display:flex;
	justify-content:center;
	align-items:center;
}
.list-img > img{
	width:10vw;
	height:10vw;
}

.list-text{
	color:#555;
}
</style>



