<template>
  <div class="shoppingCart">
		<tabbar @showtab='showtab' :cartdata='cartdata' @change='change'></tabbar>
		<cartitem :cartdata='cartdata' :show='show' @change='change'></cartitem>
		<settlement :show='show' :cartdata='cartdata' ></settlement>
  </div>
</template>

<script>
import cartitem from './cartitem'
import settlement from './settlement'
import tabbar from './tabbar'
export default {
  name: 'shoppingCart',
  data () {
    return {
			cartdata:[],
			show:true
    }
  },
	watch:{
		
	},
	components:{
		cartitem,
		settlement,
		tabbar,
	},
	methods:{
		getcartData(){
			this.axios({
				url:'shopCart',
				method:'get'
			}).then((res)=>{
				console.log(res.data)
				this.cartdata = res.data.data
			})
		},
		change(data){
			console.log(this.cartdata[data].flag)
		},
		showtab(data){
			this.show = data;
			for(let i of this.cartdata){
				i.flag = false;
			}
		}
	},
	created(){
		this.getcartData();
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.shoppingCart{
	
}
</style>



