<template>
  <div id="app">
    <router-view class='content'/>
		<div v-if='show' class='footer'>
			<div @click.stop='change(index)' class='footer-item' v-for='(item,index) in menus' :class='index == menuFlag ? "selectMenu":""'>
				<img :src='index == menuFlag ? item.selectImgUrl : item.imgUrl' class='footer-img'>
				<div>{{item.text}}</div>
			</div>
		</div>
  </div>
</template>

<script>
export default {
  name: 'App',
	created:function(){
		// this.axios.defaults.baseURL = 'http://api.juanpao.com/';
		 this.axios.defaults.baseURL = 'http://192.168.188.61:3000/';
		// this.axios.defaults.baseURL = 'http://192.168.188.12/';
		let flag = false;
		for(let i in this.menus){
			if(this.$route.name == this.menus[i].url){
				this.menuFlag = i;
				flag = true;
			}
		}
		this.show = flag;
	},
	data(){
		return{
			menus:[
				{text:'',imgUrl:'/public/image/menu/shouye1.png',selectImgUrl:'/public/image/menu/shouye.png',url:'index'},
				{text:'',imgUrl:'/public/image/menu/fenlei1.png',selectImgUrl:'/public/image/menu/fenlei.png',url:'classification'},
				{text:'',imgUrl:'/public/image/menu/car1.png',selectImgUrl:'/public/image/menu/car.png',url:'shoppingCart'},
				{text:'',imgUrl:'/public/image/menu/wode1.png',selectImgUrl:'/public/image/menu/wode.png',url:'my'},
			],
			menuFlag:0,
			show:true
		}
	},
	methods:{
		change(data){
			this.menuFlag = data;
			this.$router.push({name:this.menus[data].url});
      this.getconfig()
		},
    getconfig:function(){
			let that = this;
			that.axios({
				url:localStorage.getItem('url') + 'wxConfig',
				headers:{
					"content-type": "application/x-www-form-urlencoded",
					'Access-Token': localStorage.getItem('jwt')
				},
				method:'get'
			}).then(function(re){
				wx.config({
						debug: true, // ,apialertpclogpc
						appId: re.data.data.appid, // 
						timestamp: re.data.data.time, // 
						nonceStr: re.data.data.nonceStr, // 
						signature: re.data.data.signature,// 
						jsApiList: ['getLocation','getLocalImgData','startRecord','stopRecord','onVoiceRecordEnd','playVoice','stopVoice','chooseImage','uploadImage','uploadVoice'] // JS
				});
			})
		}
	},
	watch:{
		$route(to,from){
			let flag = false;
			for(let i in this.menus){//
				if(to.name == this.menus[i].url){
					this.menuFlag = i;
					flag = true;
				}
			}
			this.show = flag;
			console.log(flag)
		}
	}
}

</script>

<style>

*{
	margin:0;
	padding:0;
}
input:focus{
	outline:none;
}
textarea:focus{
	outline:none;
}
input[type='checkbox']{
	width:5vw;
	height:5vw;
	border-radius:50%;
	appearance:none;
	border:1px solid #999;
}
input[type='checkbox']:checked{
	width:5vw;
	height:5vw;
	background:url('/static/image/input/selected.png') no-repeat center;
	background-size:80% 80%;
}

.footer{
	position:fixed;
	bottom:0vw;
	left:0vw;
	height:12vw;
	display:flex;
	align-items:center;
	justify-content:center;
	width: 100%;
	background:#fff;
	border-top:1px solid #eee;
	font-size:2vw;
}
.footer-item{
	flex:1;
	display:flex;
	flex-flow: column wrap;
	align-items:center;
	justify-content:center;
}
.footer-img{
	width:6vw;
}
.selectMenu{
	color:#69c2aa;
}
.flex-row{
	display:flex;
	align-items:center;
	justify-content:flex-start;
}
.flex-row-wrap{
	display:flex;
	align-items:center;
	justify-content:flex-start;
	flex-flow:row wrap;
}
.flex-row-between{
	display:flex;
	align-items:center;
	justify-content:space-between;
	flex-flow:row wrap;
}
.flex-column-between{
	display:flex;
	align-items:flex-start;
	justify-content:space-between;
	flex-flow:column wrap;
}
.align-top{
	align-items:flex-start;
}
.mask{
	z-index:6;
	position:fixed;
	bottom:0vw;
	left:0vw;
	background:rgba(0,0,0,0.5);
	width:100vw;
	height:100vh;
}
.layer{
	width:96vw;
	height:80vh;
	padding:0vw 2vw;
	padding-top:2vw;
	background:#fff;
	position:fixed;
	bottom:0vw;
	border-top-left-radius:2vw 2vw;
	border-top-right-radius:2vw 2vw; 
}
.layer-buttom-btn{
	border-radius:5vw;
	background:linear-gradient(to right,#ff7301,#ff4b03);
	display:flex;
	justify-content:center;
	align-items:center;
	color:#fff;
	width:96vw;
	font-size:4vw;
	height:5vh;
}
</style>



