<template>
  <div class="store">
		<div class='store-item'>
			<img :src='data.imgUrl' class='store-img'>
			<div class='store-name'>{{data.name}}</div>
			<div class='store-btn'></div>
			<div class='store-btn store-btn-white'></div>
		</div>
		<div class='flex-row-between'>
			<div> <span class='store-score'>{{data.baby}}</span></div>
			<div> <span class='store-score'>{{data.seller}}</span></div>
			<div> <span class='store-score'>{{data.logistics}}</span></div>
		</div>
  </div>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import {swiper, swiperSlide} from 'vue-awesome-swiper'
export default {
  name: 'goodsDetail-store',
  data () {
    return {
			data:[]
    }
  },
	methods:{
		getData:function(){
			this.axios({
				method:'post',
				url:'getStoreData',
				data:{
					id:1
				}
			}).then((res)=>{
				console.log(res)
				this.data = res.data.data;
			})
		}
	},
	created(){
		this.getData();
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.store{
	background:#fff;
	margin-top:1vw;
	display:flex;
	flex-flow:column wrap;
	justify-content:flex-start;
	align-items:center;
	padding:2vw;
}
.store-item{
	display:flex;
	width:96vw;
	justify-content:flex-start;
	align-items:center;
	margin-bottom:4vw;
}
.store-img{
	width:15vw;
	height:15vw;
	border-radius:1vw;
}
.store-name{
	margin:0vw 1vw;
	font-size:3.5vw;
}
.store-btn{
	font-size:3.5vw;
	padding:1vw 2vw;
	margin:2vw;
	border:1px solid #98e6e6;
	border-radius:1vw;
	color:#39e2cb;
	font-weight:500;
}
.store-btn-white{
	background:linear-gradient(to bottom,#22dfc6,#31cfcd);
	color:#fff;
}
.flex-row-between{
	width:96vw;
	font-size:3.5vw;
	color:#999;
}
.store-score{
	font-size:3.2vw;
	color:red;
}
</style>



