<template>
  <div class="address">
		<div class='address-title'>
			<img src='/public/image/input/back.png' class='back' @click.stop='hide'>
			<div>{{id=='add' ? "" : ""}}</div>
			<div @click.stop='submit'></div>
		</div>
		<div class='area'>
			<div class='label'></div>
			<input type='text' class='informartion' v-model='item.name'>
		</div>
		<div class='area'>
			<div class='label'></div>
			<input type='number' class='informartion' v-model='item.phone'>
		</div>
		<div class='area'>
			<div class='label'></div>
			<div class='informartion' @click.stop='show'>{{item.province}} {{item.city}} {{item.area}} {{item.street}}</div>
		</div>
		<div class='area'>
			<div class='label'></div>
			<textarea type='number' class='informartion' v-model='item.address'></textarea>
		</div>
		<div class='area'>
			<div class='label'></div>
			<input type='checkbox' class='informartion checkbox' v-model='item.default'>
		</div>
		<div class='mask2' v-show='flag' id='mask'></div>
		<div v-if='editflag' class='remove' @click='showRemove'></div>
		<selectcity v-if='selectFlag' @selectHide='selectHide' @address='address'></selectcity>
  </div>
</template>

<script>
import selectcity from'./selectcity'
import { MessageBox } from 'mint-ui';
export default {
  name: 'HelloWorld',
	props:['id'],
  data () {
    return {
      selectFlag:false,
			item:{
				name:'',
				phone:'',
				address:'',
				province:'',
				city:'',
				area:'',
				street:'',
				default:false
			},
			flag:false,
			editflag:false
    }
  },
	components:{
		selectcity
	},
	methods:{
		hide(){
			this.$emit('hide','yincang')
		},
		show(){
			this.selectFlag = true;             
		},
		selectHide(data){
			this.selectFlag = false;
		},
		address(data){
			this.item.province = data.province;
			this.item.city = data.city;
			this.item.area = data.area;
			this.item.street = data.street;
		},
		submit(){/**/
			let tosta = document.createElement('div');
			tosta.style.background = '#eee';
			tosta.style.display = 'flex';
			tosta.style.padding = '2vw';
			tosta.style['border-radius'] = '2vw';
			tosta.style['flex-flow'] = 'column wrap';
			tosta.style.width = '60vw';
			let _this = this;
			if(this.item.name.length == 0){
				let div = document.createElement('div');
				div.innerHTML = '';
				tosta.appendChild(div);
				this.flag = true;
			}
			if(this.item.phone.length != 11){
				let div = document.createElement('div');
				div.innerHTML = '';
				tosta.appendChild(div);
				this.flag = true;
			}
			if(this.item.address.length == 0){
				let div = document.createElement('div');
				div.innerHTML = '';
				tosta.appendChild(div);
				this.flag = true;
			}
			/*if(this.item.detailedAddress.length == 0){
				let div = document.createElement('div');
				div.innerHTML = '';
				tosta.appendChild(div);
				this.flag = true;
			}*/
			if(this.flag != true){
				this.post();/**/
			}
			document.getElementById('mask').appendChild(tosta);
			window.setTimeout(()=>{
				_this.flag = false;   
				document.getElementById('mask').removeChild(tosta)
			},2000)
		},
		post(){
			this.axios({
				url:'',
				data:this.item,
				method:'post'
			}).then((res)=>{
				console.log(res);
			})
		},
		get(){
			this.axios({
				url:'getAddressItem',
				method:'get'
			}).then((res)=>{
				console.log(res.data.data);
				this.item = res.data.data;
			})
			this.editflag = true
		},
		showRemove(){
			MessageBox.confirm('?').then(action => {
				console.log("")
				this.$emit('hide','yincang')
			});
		}
	},
	created(){
		this.id == 'add'? console.log('a'): this.get();
	},
	watch:{
		
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.address{
	background:#fff;
	padding-bottom:2vw;
}
input{
	outline:none;
	border:0px solid red;
	background:none;
}
.address-title{
	display:flex;
	justify-content:space-between;
	align-items:center;
	border-bottom:1px solid #eee;
	padding:3vw 2vw;
}
.back{
	width:5vw;
	height:5vw;
}
.area{
	padding:4vw 2vw;
	display:flex;
	flex-flow:row wrap;
	border:1px solid #eee;
}
.label{
	width:20vw;
	font-size:3.3vw;
	font-weight:600;
	color:#313133;
}
.informartion{
	width:75vw;
	word-wrap:break-word;
	border:0px solid red;
	font-size:3.5vw;
}
.checkbox{
	position:absolute;
	right:2vw;
	width:15vw;
	height:5vw;
	border-radius:5vw;
	appearance:none;
	border:1px solid #eee;
	background:#fff url('/public/image/input/circle.png') no-repeat left;
	background-size:33% 100%;
}
.checkbox:checked{
	width:15vw;
	height:5vw;
	border:1px solid red;
	background:#fff url('/public/image/input/selectCircle.png') no-repeat right;
	background-size:33% 100%;
}
.mask2{
	z-index:6;
	position:fixed;
	bottom:0vw;
	left:0vw;
	background:rgba(0,0,0,0.5);
	width:100vw;
	height:100vh;
	display:flex;
	justify-content:center;
	align-items:center;
}
.remove{
	font-size:3.2vw;
	font-weight:700;
	color:red;
	padding:2vw;
}
</style>



